<template>
    <a-layout class="main_page">
        <a-layout-header style="background-color: #00BFFF"> <h1>{{ msg }}</h1> </a-layout-header>
        <a-layout-content>            
            <a-row justify="center">
                <a-col :span="18" >

                <div class="controlPanel" style="display: flex; justify-content: center;">
                    <a-card title="Parameters Setting For Deep Learning in Databases" :bordered="false" class="cardContainer">
                        <!-- Gudiance on using gui -->
                        <template v-if="showTutorial">

                            <a-steps v-model:current="currentStep" >
                                <a-step title="Step 1" description="Introduction" /> 
                                <a-step title="Step 2" description="Preparation" />
                                <a-step title="Step 3" description="Retreiving result" />
                            </a-steps>

                            <div class="stepContent">
                                <a-descriptions title="" v-if="currentStep === 0">
                                    <a-descriptions-item>
                                        <div class="stepContent">
                                            <span class="stepOne">
                                            This project aims to assist researchers to select queries,<br> choose datasets with availiable models to <mark>explore query optimization results.</mark><br/>Follow this guidance to investigate performance of NNGP model in optimizing query planning and execution.<br/>Know how to use?<br/>Just click the <a-button style="margin-right: 3px; margin-left: 3px" @click="viewTutorial">
                                                {{ showTutorial ? "Close" : "Guidance" }}</a-button> to skip this guidance.</span>
                                        </div>
                                    </a-descriptions-item>
                                </a-descriptions>
                            </div>

                            <div class="stepContent">
                            <a-descriptions v-if="currentStep === 1">
                                <a-descriptions-item >
                                    <div class="stepContent">
                                        <span class="stepTwo">
                                        Before actually running the nngp model and compare with the PostgreSQL Planner, <br/>
                                        First, you <mark><strong>MUST</strong></mark> generate sets of training and testing queries for exploration.  <br/>
                                        <mark>Choose Schema</mark>: TPCDS and IMDB are provided, Choose schema to view more<br/>
                                        <mark>min-max numbers of joining </mark> (Numbers of Minimum, Maximum join in query)<br/>
                                        <mark>Equi-join and Non-equi-join ratio:</mark> (Only provided when minimum join of 0 is selected)<br/>
                                        <mark>Train and Test query ratio:</mark> (base numbers of query is 3000, if you choose 20% for test ratio)<br/>
                                        You will get only 3000 * 20% = 600 query maximum for test<br/>
                                        After selecting all required parameters, server will generate the query. <br/>
                                        </span>
                                    </div>
                                </a-descriptions-item>
                            </a-descriptions>
                            </div>
                            <div class="stepContent">
                            <a-descriptions v-if="currentStep === 2">
                                <a-descriptions-item >
                                    <div class="stepContent">
                                        <span class="stepThree">
                                        After generating query and select the model running parameters<br/>
                                        You can now see the result in table if everything run properly.<br/>
                                        You can click the <a-button>image</a-button> button for detail performance in graphs<br/>
                                        You can also click the <a-button type='primary' >Plan Tree</a-button> button for a better investigation into each query exeuction
                                        </span>
                                    </div>
                                </a-descriptions-item>
                            </a-descriptions>    
                            </div>
                        </template>

                        <template v-if="!showTutorial">Guidance will be shown here: 
                            <a-button style="margin-left: 3px" @click="viewTutorial">
                                {{ showTutorial ? "Close" : "Guidance" }}</a-button>
                        </template>

                    <div class="paraSelect">

                    <a-divider style="background-color: #00BFFF" ></a-divider>

                        <template v-if="isGenerate"> <!-- Generating  control -->
                            <p>***Genreate Query for Testing First : </p>
                            <div class="selection">
                                <span class="Option_data_title">Choose a schema: </span>
                                <!-- <a-cascader v-model="selectDataSet" @change="changeDataSet" :options="dataStrings" placeholder="Select a dataset." ></a-cascader> -->
                                <a-select v-model="selectDataSet" @change="changeDataSet" :options="dataStrings" placeholder="Select a schema."></a-select> 
                            </div>
                            <div class="selection">
                                <span class="Option_data_title">Choose Minimum Join: </span>
                                <a-select v-model="min_join" @change="changeMin" :options="minStrings" placeholder="Select a number."></a-select> 
                            </div>
                            <div class="selection">
                                <span class="Option_data_title">Choose Maximum Join: </span>
                                <a-select v-model="max_join" @change="changeMax" :options="maxStrings" placeholder="Select a number."></a-select> 
                            </div>
                            <div class="slideControl">
                                <span>Move the slider for train_test ratio: [train: <mark>{{this.train_ratio}}%</mark>, test: <mark>{{ 100 - this.train_ratio}}%</mark>]  </span>
                                <a-slider v-model:value="train_ratio" class="train_test_ration_title"
                                :marks="marksSlider" :tooltip-visible="false" :step="5" />
                            </div> 
                            <div class="slideControl" v-if="this.min_join === 1">
                                <span>Move the slider for Join_Non-Join query ratio: [join: <mark>{{this.join_ratio}}%</mark>, non-join: <mark>{{ 100 - this.join_ratio}}%</mark>]  </span>
                                <br />
                                <a-slider v-model:value="join_ratio" class="train_test_ration_title"
                                :marks="marksSlider" :tooltip-visible="false" :step="5" />
                            </div>
                            <a-button type='primary' ghost @click="ratioSelection">Generate Query </a-button>
                            <a-button type='primary' style="margin-left: 6px" @click="switchMode">To Testing</a-button>
                        </template>

                        <template v-if="!isGenerate"> <!-- Testing control -->
                            <p> <mark>Retrain Model before Running</mark>Selecting parameters for Testing: </p>
                            <div class="selection">
                                <span class="Option_model_title">Choose a Model: </span>
                                <a-select v-model="selectModel" @change="changeModel" :options="modelStrings" placeholder="Select a model."></a-select> 
                            </div>
                            <div class="selection">
                                <span class="Option_data_title">Choose a dataset: </span>
                                <a-select v-model="selectDataSet" @change="changeDataSet" :options="dataStrings" placeholder="Select a dataset."></a-select> 
                            </div>
                            <div class="selection">
                                <span class="Option_query_title">Choose the number of table(s) to be joined: </span>
                                <a-cascader v-model="selectQuery" @change="changeQuery" :options="queryStrings" placeholder="Select query set."></a-cascader>                    
                            </div>
                            <div class="selection">
                                <span class="Option_query_num_title">Choose the number of queries: (enter multiple of 10)</span>
                                <!-- <a-select v-model="selectQueryNum" @change="changeQueryNum" :options="querynumStrings" placeholder="Select query number."></a-select>  -->
                                <a-input-number v-model:value="selectQueryNum" :min="10" :max="1000" step="10" />

                            </div>
                            <div class="buttonContainer">
                                <a-button type='dashed' @click="retrainConfirm">Retrain Model</a-button>
                                <a-button style="margin-left: 6px" type='primary' @click="confirm">Run Query</a-button>
                                <a-button style="margin-left: 6px" danger ghost @click="clean">Clean Data</a-button> 
                                <a-button style="margin-left: 6px" type='dashed' @click="switchMode">Back to Generate</a-button>
                            </div>
                        </template> 
<!--                    <template v-else>
                                <div>
                                <a-progress type="circle" :percent="75" :format="percent => `${percent} Days`" />
                                <a-progress type="circle" :percent="100" :format="() => 'Done'" />
                                </div>
                                <a-button type='primary' @click="switchMode">Train Model</a-button>
                        </template>  -->
                    </div> <!-- class paraSelect -->

                    </a-card>

                </div> <!-- end of controlPanel-->

                <a-divider style="background-color: #00BFFF" ></a-divider>
                <!-- Confirm dialogue for running model for running model -->
                <a-modal v-model:visible="modelVisible" title="Query Running" 
                ok-text="confirm" cancel-text="cancel" @ok="runModel">
                <p>
                    You are running model <span class="highlightText">{{selectModel}}</span>, 
                    with dataset <span class="highlightText">{{selectDataSet}}</span>,
                    joining <span class="highlightText">{{selectQuery}}</span> tables 
                    with <span class="highlightText">{{selectQueryNum}}</span> query.
                </p>
                </a-modal>
                <a-modal v-model:visible="retrainVisible" title="Model Retrain" 
                ok-text="confirm" cancel-text="cancel" @ok="retrainModel">
                <p>
                    You are retraining model
                </p>
                </a-modal>

                <!-- show schema -->
                <template v-if="selectDataSet === 'IMDB'">
                    <div class="schemaDisplay">
                        <span>Dataset: {{ this.selectDataSet }}</span>
                        <span>{{ checked ? "Hide" : "Show" }} Schema <a-switch v-model:checked="checked" style="margin-left: 12px;"/></span>
                    </div>
                    <a-table v-if="checked" :columns="tableSchema"  :dataSource="ImdbSchema" :pagination=false ></a-table> <br/>
                    <a-image v-if="checked" :width="750" src="http://137.189.59.166:8000/imdb.png" />
                </template>
                <template v-else-if="selectDataSet === 'TPCDS'">
                    <div class="schemaDisplay"> 
                        <span>  Dataset: {{ this.selectDataSet }}</span>
                        <span>{{ checked ? "Hide" : "Show" }} Schema <a-switch v-model:checked="checked" style="margin-left: 12px;"/></span>
                    </div>
                    <a-table v-if="checked" :columns="tableSchema" :dataSource="TpcdSchema" :pagination=false ></a-table> <br/>
                    <a-image v-if="checked" :width="750" src="http://137.189.59.166:8000/tpcds.png" />
                </template>
                <template v-else><span class="schemaRemind">Schema will be shown when you select the dataset</span></template>

                <a-divider style="background-color: #00BFFF" ></a-divider>

                <!-- response data as a table -->
                <template v-if="isShow">

                    <p>
                    Result of running model <span class="highlightText">{{selectModel}}</span>, 
                    with dataset <span class="highlightText">{{selectDataSet}}</span>,
                    joining <span class="highlightText">{{selectQuery}}</span> tables 
                    with <span class="highlightText">{{selectQueryNum}}</span> query.
                    <a-button @click="generateImage();"> image</a-button>
                    </p>

                    <template v-if="isImageShow">
                    <p>Q-Error of Running Result</p>
                    <a-image :height="200" :width="350"
                        :src="var_q_errorImg" />
                    <br>
                    <br>
                    <p>Average Planning Time and Execution Time Comparsion</p>
                    <a-image :height="225" :width="550"
                        :src=avgImage />
                    <a-image :height="450" :width="550"
                        :src=planImage />
                    <a-image :height="300" :width="550"
                        :src=execImage />
                    <br>
                    <br>
                    <br>
                    <p>Top 10 lowest and highest Queries performance</p>
                    <a-image :height="150" :width="350"
                        :src="var_exec_low" />
                    <a-image :height="150" :width="350"
                        :src="var_exec_high" />
                    <a-image :height="150" :width="350"
                        :src="var_plan_low" />
                    <a-image :height="150" :width="350"
                        :src="var_plan_high" />
                    </template>

                    <!-- display table -->
                    <a-table :columns="reponseDataColumns" :dataSource="displayData" @change="headerChange" >
                        <template #bodyCell="{ column, record }">
                            <template v-if="column.key === 'action'">
                                <a-button type='primary' @click="requestPlanTree(record.key)">Plan Tree</a-button>
                            </template>
                        </template>
                    </a-table>

                </template>

                </a-col>
             </a-row>
             <!-- back into top place -->
            <a-back-top />
        </a-layout-content>

                    <a-layout-footer>
                        @seem4999
                    </a-layout-footer>

    </a-layout>
</template>


<script>
import { modelStrings, dataStrings, queryStrings, querynumStrings, tableSchema, ImdbSchema, TpcdSchema, reponseDataColumns, ForestSchema, minStrings, maxStrings } from '@/data/optionstr';
import { GUIService } from '@/services/services';
import { http } from '@/services/http_common';
import axios from "axios";
import { ref } from 'vue';
import { message } from 'ant-design-vue';


export default {
    name: "SelectComponent",
    setup() {
        const marksSlider = ref({
            0: '0 %',
            25: '25 %',
            50: '50 %',
            75: '75 %',
            100: '100 %'  
        });
        return {
            marksSlider
        }
    },
    data() {
        return {
            click_msg: "SEEM FYP Project", // title 
            // parameter options 
            modelStrings, dataStrings, queryStrings, querynumStrings, minStrings, maxStrings,
            // Schema options
            tableSchema, ImdbSchema, TpcdSchema, ForestSchema,
            // schemea: "Schema will be shown here", 
            // selection control:
            selectModel: 'NNGP', selectDataSet: '', // imdb or tpcds
            selectQuery: '', selectQueryNum: '',
            // Gui Control
            displayData: "", // json data
            reponseDataColumns, // table to be shown
            isShow: false, // to display result
            modelVisible: false, // show confirm dialogue
            checked: true, // showing schmea or not 
            retrainVisible: false,

            // reponse image source
            var_tpcds: "", var_imdb: "",
            var_avgImg:"",
            var_planImg:"", var_plan_low:"", var_plan_high:"",
            var_execImg:"", var_exec_low:"", var_exec_high:"",
            var_q_errorImg:"",
            // guidance 
            showTutorial: true,
            currentStep: 0,

            isRetrain: false, 
            // query genreation and model running

            train_ratio: 80,
            join_ratio: 50,
            min_join:"", max_join:"",
            generate_query: false,
            response_message: "",
            isGenerate: true,
        }
    },
    components: {
    },
    computed: {
        msg() { return this.click_msg },
        imgSource4() {
            return this.var_planImg;
        },
        avgImage() {
            console.log("average image link changed")
            return this.var_avgImg;
        },
        planImage() {
            return this.var_planImg;
        },
        execImage() {
            return this.var_execImg;
        },
    },
    methods: {
        run4() {
            GUIService.get4().then(response => { 
                // this.imgSource4 = "data:image/png;base64," + response.data;
                // this.var_imgSource4 = "";
                // this.var_imgSource4 = response.data;
                console.log(response)
            }).catch(e => {console.log(e);});
        },
        clean() {
            this.isShow = false;
            GUIService.getClean().catch(e => {console.log(e); });
        },
        // run the postgresql and nngp model by sending responsing parameters
        runModel(){
            console.log("selectDataSet: " + this.selectDataSet)
            console.log("selectQuery: " + this.selectQuery)
            console.log("selectQueryNum: " + this.selectQueryNum)
            if(!this.isRetrain) {
                window.alert("You should retain the model first !!! ");
            }
            if ( this.selectQuery > this.max_join || this.selectQuery < this.min_join)  {
                window.alert(`Numbers of table of join should be bewteen max_join: ${this.max_join} and min_join: ${this.min_join}`);

            } 
            else {
                if ( this.selectQueryNum % 10 === 0) {

                    if ( this.selectDataSet && this.selectQuery && this.selectQueryNum) {
                        http.get(`/runModel/${this.selectDataSet}-${this.selectQuery}-${this.selectQueryNum}`)
                        .then(response => {
                            console.log(response.data);
                            this.displayData = response.data; // json data 
                            this.isShow = true;
                        }).catch(e => { console.log(e); })

                        this.modelVisible = false;
                        console.log("running model")
                    } else {
                        this.modelVisible = false;
                        window.alert("Please select all parameters");
                    }

                } else {
                    window.alert("You should input a valid number");
                }   
            }

     
        },        
        retrainModel(){
            console.log("from retrain model")
            console.log("selectDataSet: " + this.selectDataSet)

            this.isRetrain = true;

            if ( this.selectDataSet) {
                console.log(`/retrainModel/${this.selectDataSet}`)
            http.get(`/retrainModel/${this.selectDataSet}`)
            .then(response => {
                this.show_message("The model has been retrained")
                console.log(response.data);
            }).catch(e => { console.log(e); })

            this.retrainVisible = false;
            console.log("retrain model")
            } 
        },
        // onChange methods for selecting params
        changeModel(value) {
            this.selectModel = value;
            console.log("selectModel: "+this.selectModel);
        },
        changeDataSet(value){
            this.selectDataSet = value;
            console.log(this.selectDataSet);
            if (this.selectDataSet === "IMDB") {
                    console.log("using imdb")
            }   
            else if(this.selectDataSet === "TPCDS") {
                console.log("using tbcds")
            }
        },
        changeQuery(value){ this.selectQuery = value; },
        changeQueryNum(value) { this.selectQueryNum = value; },
        changeMin(value) {
            this.min_join = value;
         },
        changeMax(value) { 
            this.max_join = value; 
            if ( this.min_join > this.max_join) {
                window.alert("Numbers of maximum join should not be smaller than that of minimum join");
                this.max_join = this.min_join;
            }
        },
        // for confirming the model running parameters
        confirm() { this.modelVisible = true },
        retrainConfirm() { this.retrainVisible = true },

        headerChange: (pagination, filters, sorter) => {
            this.displayData = this.displayData.sort()
            console.log("params: " , pagination, filters, sorter); 
        },
        viewTutorial() {
            if (this.showTutorial){
                this.showTutorial = false; 
            } else {
                this.showTutorial = true;
            }
        },
        // generation image 
        generateImage(){
            const planningtime = this.displayData.map(data => data.Planning);
            const nngpPlanning = this.displayData.map(data => data.NNGP_Planning);
            const execution = this.displayData.map(data => data.Execution);
            const nngpExecut = this.displayData.map(data => data.NNGP_Execution);
            axios.post(
                'api/image',
                {   
                    plan: planningtime,
                    execution: execution,
                    nngp_plan: nngpPlanning,
                    nngp_execution: nngpExecut
                },
            ).then((response) => {
                let datasoruce = response.data;
                // problem on updating image
                this.var_avgImg = "", this.var_q_errorImg = "",
                this.var_execImg = "", this.var_exec_high = "", this.var_exec_low = "";
                this.var_planImg = "", this.var_plan_high = "", this.var_plan_low = "";
                console.log("the var_img: " + this.var_avgImg)
                console.log("the image source set to be empty")

                // this.isImageShow = false;

                // get image again from server response 
                setTimeout(() => {
                    this.var_avgImg = datasoruce.average;
                    this.var_execImg = datasoruce.execution;
                    this.var_planImg = datasoruce.planning;
                    this.var_exec_high = datasoruce.high_exec;
                    this.var_exec_low = datasoruce.low_exec;
                    this.var_plan_high = datasoruce.high_plan;
                    this.var_plan_low = datasoruce.low_plan;
                    this.var_q_errorImg  = datasoruce.q_error;
                    this.isImageShow = true;
                    console.log("Getting image again")
                }, 1200)
                console.log("var_q_errorImg: " + this.var_q_errorImg)

                console.log(response);      
            }).catch(error => { console.log(error) })
        },
        switchMode(){
            if (this.isGenerate) {
                this.isGenerate = false;
            } else {
                this.isGenerate = true;
            }
        },
        ratioSelection(){
            let train_percent = (this.train_ratio / 100)  ;
            let test_percent = (100 - this.train_ratio) / 100;
            let join_percent = (this.join_ratio / 100);
            let non_join_percent = (100 - this.join_ratio) / 100;
            let min = this.min_join;
            let max = this.max_join;

            // safety prevention on the implementation 
            if ( min === "" ) { min = 1; }
            if ( max === "") { max = 5; }

            // console.log("train_percent: " + train_percent); console.log("test_percent: " + test_percent); console.log("join_percent: " + join_percent); console.log("non_join_percent: " + non_join_percent);
            if ( this.selectDataSet && (min <= max) ) {
                axios.post(
                'api/ratio',
                    {   
                        relation_name: [this.selectDataSet],
                        train_test_ratio: {
                            "train": [ train_percent ],
                            "test" : [ test_percent ]
                        },
                        non_join_query_ratio : {
                            "join": [ join_percent ],
                            "non_join" : [ non_join_percent ]
                        },
                        min_max : {
                            "min": [ min ],
                            "max": [ max ]
                        }
                    },
                ).then((response) => {
                    console.log(response)
                    this.response_message = response.data.message;
                    this.show_message(this.response_message)
                    this.isRetrain = true;
                }).catch(error => { console.log(error) })
            } else {
                window.alert("Select all parameters and make sure your input is valid ");
            }
        },
        show_message(content) {
            message.success(content, [2]) // [ second ]
        },
        next() {
            this.currentStep++;
        },
        prev() {
            this.currentStep--;
        },
        requestPlanTree(key) {
            axios.post(
                'api/planTree',
                    {   
                        selectQuery: this.selectQuery,
                        queryNumber: key
                    },
                ).then((response) => {
                    console.log(response);
                    let pdf_link = response.data.pdf;
                    window.open(pdf_link, '_blank');
                }).catch(error => { console.log(error) })
            }
        },

}
</script>

<style>
